.Header {
  display: flex;
  justify-content: space-between;
}

.Header img {
  width: 5rem;
  padding: 1rem;
}

.Header img:hover {
  transform: scale(1.3, 1.3);
  transition: 1s;
}

.Header .Header-sections {
  display: flex;
  align-items: center;
  padding: 15px 4px;
}

.Header .Header-sections a {
  text-decoration: none;
  color: inherit;
  margin: 0 6px;
  font-size: 1.1rem;
}

.Header .Header-sections a.active {
  color: #fff498;
  font-size: 1.2rem;
}

/* MEDIA QUERY FOR RESPONSIVE DESIGN */

@media only Screen and (min-width: 250px) and (max-width: 450px) {
  .Header img {
    width: 1.5rem;
  }
  .Header .Header-sections a,
  .Header .Header-sections a.active {
    font-size: 0.9rem;
    margin: 0 3px;
  }
}

